import { Suspense, lazy } from "react";
import { Routes, Route, Outlet, Link } from "react-router-dom";
const About = lazy(() => import('./pages/About'));
const Dashboard = lazy(()=>import("./pages/Dashboard")) 
export default function LazyLoad() { 
  return (
    <div>
      <h1></h1>
      <p>
         
      </p>
      <p>
      About  import() <code>&lt;React.Suspense fallback&gt;</code> 
      </p>
      <p>
        Dashboard  React Router <code>&lt;Route&gt;</code><code>&lt;Routes&gt;</code>  *  <code>*</code> like <code>&lt;Route path="dashboard/*"&gt;</code> 
      </p>
      <Routes>
        <Route path="" element={<Layout />}>
          <Route index element={<Home />} />
          <Route path="about" element={
            <Suspense fallback={<>...</>}>
              <About/>
            </Suspense>
           } />
          <Route path="dashboard/*" element={ 
            <Suspense fallback={<>...</>}>
              <Dashboard/>
            </Suspense>
          } />
          <Route path="*" element={<NoMatch />} />
        </Route>
      </Routes>
    </div>
  )
}

function Layout(){
  return (
    <div>
      <nav>
        <ul>
          <li><Link to=''>Home</Link></li>
          <li><Link to='about'>About</Link></li>
          <li><Link to="dashboard/messages">Messages (Dashboard)</Link></li>
        </ul>
      </nav>
      <hr />
      <Outlet/>
    </div>
  )
}

function Home() {
  return (
    <div>
      <h2>Home</h2>
    </div>
  );
}

function NoMatch() {
  return (
    <div>
      <h2>Nothing to see here!</h2>
      <p>
        <Link to="">Go to the home page</Link>
      </p>
    </div>
  );
}