import {
  Outlet,
  Link,
  useNavigation,
} from "react-router-dom";
export default function RouterProviderLazy() {
  const nav = useNavigation();
  return (
    <div>
      <h1>RouterProvider</h1>
      <p>
        <code>route.lazy()</code> 
      </p>
      <p>
        "About""Dashboard" <code>import()</code> 
      </p>
      <p>
        / <code>action</code>, <code>loader</code>, <code>element</code>
        , <code>errorElement</code>,  <code>shouldRevalidate</code><code>lazy()</code> <code>path</code>, <code>index</code>, <code>children</code>, {" "}
        <code>caseSensitive</code>
      </p>
      <div style={{ position: "fixed", top: 0, right: 0 }}>
        {nav.state !== "idle" && <p>Navigation in progress...</p>}
      </div>
      <nav>
        <ul>
          <li>
            <Link to=''>Home</Link>
          </li>
          <li>
            <Link to="about">About</Link>
          </li>
          <li>
            <Link to="dashboard/messages">Messages (Dashboard)</Link>
          </li>
        </ul>
      </nav>
      <hr />
      <Outlet/>
    </div>
  )
}

